  methods: {
    capture(e) {
      let posts = document.querySelectorAll(".each-post .post-image");
      this.scrolling = true;
      setInterval(() => {
        if (this.scrolling) {
          posts.forEach(el => {
            // el.getBoundingClientRect().bottom  < (window.pageYOffset + window.innerHeight - (window.innerHeight / 1.71337)) ? el.classList = 'post-image animated' :  el.classList = 'post-image'; TODO MAYBEEE OPTIMIZE HEREE
            if (this.SCREEN_SIZE == "desktop") {
              el.parentNode.offsetTop >
                window.pageYOffset - window.innerHeight &&
              el.parentNode.offsetTop + el.parentNode.offsetHeight <
                window.pageYOffset + window.innerHeight / 2
                ? (el.classList = "post-image animated")
                : (el.classList = "post-image");
            } else {
              window.pageYOffset + window.innerHeight >
                el.offsetParent.offsetHeight +
                  el.offsetParent.offsetTop +
                  window.innerHeight &&
              window.pageYOffset - window.innerHeight <
                el.offsetParent.offsetHeight +
                  el.offsetParent.offsetTop +
                  window.innerHeight
                ? (el.classList = "post-image animated")
                : (el.classList = "post-image");
            }
            if (el == posts[0]) {
              el.classList = "post-image animated";
            }
          });
        }
        this.scrolling = false;
      }, 600);
    }
  },
  mounted() {
    if (window.screen.width > 768) {
      this.SCREEN_SIZE = "desktop";
    } else {
      this.SCREEN_SIZE = "mobile";
    }
  },
  created() {
    window.addEventListener("scroll", this.capture);
  },
  destroyed() {
    window.addEventListener("scroll", this.capture);
  }
